.home__container{
    row-gap: 7rem;
}
.home__content{
    grid-template-columns: 116px repeat(2, 1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}
.home_social{
 display: grid;
 grid-template-columns: max-content;
 row-gap: 1rem;
}
.home__social-icon{
    font-size: 1.4rem;
    color: var(--title-color);
}

.home__social-icon:hover{
 color: var(--title-color-dark);
}
.home__title{
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}
.home__subtitle{
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 5.4rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);
}
.home__subtitle::before{
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
   background-color: var(--text-color);
   left: 0;
   top: 2rem;
}
.home__img{
    background: url(../../../assets//ap2.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgb(255 255 255/ 30%);
    order: 1;
    justify-self: center;
    width: 300px; 
    height: 300px;
    animation: profile__antimate 8s ease-in-out infinite 1s;
}
.home__hand{
    width: 38px;
    height: 38px;
    margin-left: 0.5rem;
}
.home__description{
    min-width: 400px;
    margin-bottom: var(--mb-3);
}
@keyframes profile__antimate{
0%{
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
}
100%{
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
}
50%{
    border-radius: 60% 60% 70% 40%/50% 60% 30% 60%;
}
}
.home__scroll{
    margin-left: 9.25em;
}
.wheel{
    animation: scroll 2s ease infinite;
}
@keyframes scroll{
    0%{
        transform: translateY(0);
    }
    30%{
        transform: translateY(3.75);
    }
}
.home__scroll-name{
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}
.home__scroll-arrow{
    font-size: 1.25rem;
    color: var(--title-color);
}
@media screen and (max-width: 992px) {
    .home__content{
        grid-template-columns: 100px repeat(2, 1fr);
        column-gap: 1.25rem;
    }
    .home__hand{
        width: 24px;
        height: 24px;
    }
    .home__subtitle{
     padding-left: 3.75rem;
     margin-bottom: var(--mb-1);
    }
    .home__subtitle::before{
      width: 42px;
      top: 0.9rem;
    }
     .home__img{
        width: 250px;
        height: 250px;
        box-shadow: inset 0 0 0 8px rgb(255 255 255/ 30%);
     }
     .home__scroll{
        margin-left: 7.5rem;
     }
   }
   @media screen and (max-width: 768px) {
    .home__content{
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
    }
    .home__img{
        order: initial;
        justify-self: initial;
    }
    .home__data{
        grid-column: 1/3;
    }
    .home__img{
        width: 230px;
        height: 200px;
        box-shadow: inset 0 0 0 6px rgb(255 255 255/ 30%);
    }
    .home__scroll{
        display: none;
    }
   } 
   @media screen and (max-width: 350px) {
     .home__img{
        width: 280px; ;
        height: 280px;
     }
     .home__hand{
        width: 22px;
        height: 22px;
     }
  }
  
  